@import 'variables'; 

.hide{ 
  visibility: hidden;
}
.show{ 
  visibility: visible;
}

.blurred-img {
  background-image: url('../images/tiny.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(10px);
  background-color: #005f99;
  height: 100%;
}

.blurred-img img {
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}
.blurred-img::before {
  content: "";
  position: absolute;
  inset: 0;
  opacity: 0;
  animation: pulse 2.5s infinite;
  background-color: white;
}

.blurred-img.show::before {
  animation: none;
  content: none;
}
.blurred-img.show img {
  opacity: 1;
}

@keyframes pulse {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.1;
  }
  100% {
    opacity: 0;
  }
}

h1 {
  font-size: clamp(2rem, 5vw, 4rem);
}

// Global Margin Class
.bucket {
    margin: 0 auto;
    padding: 2rem;
    max-width: 1200px;
  }

  // Hero Section Styles
  .hero-section {
    text-align: center; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 8rem;
    @media (max-width: 768px){
    padding-top: 2rem;
    }
    
    .hero-title {
      font-size: clamp(2rem, 5vw, 4rem);
      color: $black;
      margin-bottom: 1rem;
      font-weight: bold;
      transition: opacity 0.5s ease-in-out;
      min-height: 30vh;
      justify-content: center;
      align-items: center;
      @media (max-width: 768px) {
        font-size: 20px; //  on mobile/tablet 
        background-color: #FFC857;
      }
    }


    
    .hero-description {
      font-size: $font-size-description;
      color: $black;
      margin-bottom: 2rem;
      max-width: 800px;
      margin-left: auto;
      margin-right: auto;
    }
    
    .hero-buttons {
      display: flex;
      justify-content: center;
      gap: 1rem;
      margin-bottom: 3rem;
      
      .primary-btn {
        background-color: $primary;
        color: $white;
        padding: $btn-padding;
        border-radius: 0.375rem;
        font-weight: 600;
        border: none;
        cursor: pointer;
        
        &:hover {
          background-color: darken(#005f99, 10%);
          color: $white;
        }
      }
      
      .secondary-btn {
        background-color: transparent;
        color: $black;
        border: 2px solid $secondary-color;
        padding: $btn-padding;
        border-radius: 0.375rem;
        font-weight: 600;
        cursor: pointer;
        
        &:hover {
          background-color: lighten($secondary-color, 40%);
        }
      }
    }
    
    .hero-image {   
      img {
        width: 80vw;
        max-width: 80vw; 
        height: 40vh;
        max-height: 40vh;
        margin-top: 1rem;
        border-radius: 0.5rem;
        object-fit: cover;
        object-position: center;
      
      }
    }
  }
  



// Services Section Styles
.services-section {
    background-color: $primary;
    padding: 6rem 4rem;
    text-align: center;
    color: $black;
  
    .section-header {
      font-size: 2rem; 
      margin-bottom: 1rem;
      font-weight: 700;
      color: $white;
      margin: 4rem auto;
    }
  
    .section-description {
      font-size: 1.125rem;
      color: lighten($white, 20%);
      max-width: 800px;
      margin: 0 auto 3rem;
    }
  
    .services-grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr); // 3 columns on larger screens
        gap: 1.5rem; // Space between cards
    
        @media (max-width: 1024px) {
          grid-template-columns: repeat(2, 1fr); // 2 columns on medium screens
        }
    
        @media (max-width: 768px) {
          grid-template-columns: 1fr; // Single column on mobile/tablet
        }
      }
  }
  
  // Service Card Styles

  .service-card {
    background-color: $card-bg-color;
    border-radius: 15px;
    padding: 1.5rem;
    flex: 1;
    max-width: 100%;
    text-align: start;
    color: $white;
    transition: transform 0.3s ease, box-shadow 0.3s ease;

    @media (max-width: 768px) {
      max-width: 100%;
    }

    &:hover {
      transform: translateY(-10px);
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
      color: black;
      background-color: white;
    }

    img {
      max-width: 100%;
      border-radius: 10px;
      margin-bottom: 1rem;
      margin-top: 1rem;
    }

    h3 {
      font-size: 1.5rem;
      margin-bottom: 0.8rem; 
    }

    p {
      font-size: 0.9rem; 
    }
  }


  .why-us-section {
    background-color: $background-color;
    padding: 4rem 2rem;
    color: $white; 
  
    .section-title {
      font-size: 2.5rem;
      margin-bottom: 1.5rem;
      color: black;
  
      .highlight {
        color: $primary; // Accent color for the "Choose Us" part
      }
    }
  
    .section-description {
      font-size: 1.2rem;
      margin-bottom: 3rem;
      color: $secondary-color;
    }
  
    .cards-container {
        display: flex;
        flex-direction: column;
        gap: 3rem;
        text-align: start;
    }
  }
  
  .why-us-card {
    display: flex;
    align-items: center;
    padding: 2rem;
    border-radius: 8px;
    transition: all 0.3s ease;
  
    &:hover {
      background-color: $white;
      color: $black;
    }
  
    .text-content {
      flex: 1;
      padding: 0 2rem;
      color: $black;
  
      .card-title {
        font-size: 1.8rem;
        margin-bottom: 1rem;
      }
  
      .card-description {
        margin-bottom: 1.5rem;
      }
  
      .card-bullets {
        list-style-type: none;
        padding: 0;
  
        li {
          position: relative;
          margin-bottom: 1rem;
          padding-left: 1.5rem;
  
          &::before {
            content: '';
            position: absolute;
            left: 0;
            top: 0.5rem;
            width: 8px;
            height: 8px;
            background-color: $primary;
            border-radius: 50%;
          }
        }
      }
    }
  
    .image-content {
      flex: 1;
  
      .card-image {
        max-width: 100%;
        height: auto;
        border-radius: 8px;
      }
    }
  
    &.image-left .image-content {
      order: -1; // Move image before text
    }
  
    &.image-right .image-content {
      order: 1;
    }
  
    // Mobile responsiveness
    @media (max-width: 768px) {
      flex-direction: column; // Stack image and text vertically
      padding: 1rem;
      .image-right{
        flex-direction: column-reverse;
      }
      .text-content {
        padding: 1rem 0; // Adjust padding for mobile
        text-align: start; // Center text on mobile
  
        .card-title {
          font-size: 1.6rem; // Slightly smaller font on mobile
        }
  
        .card-description {
          margin-bottom: 1rem; // Reduce margin on mobile
        }
  
        .card-bullets {
          li {
            padding-left: 1rem; // Adjust padding for mobile
          }
        }
      }
  
      .image-content {
        .card-image {
          width: 100%; // Ensure the image takes full width on mobile
          margin-bottom: 1rem; // Add space below the image on mobile
        }
      }
    }
  }
  
  


  .footer {
    background-color: black;
    color: white;
    padding: 2rem;
  }
  
  .footer-content {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    max-width: 100vw;
    margin: 0 auto; 
    justify-content: space-between;
    align-items: center; 
  }
  

  
  .footer-column_first{
    justify-content: center;
    align-items: center;
    @media (min-width: 768px) {
        justify-content: flex-start;
      }
  }
  .footer-logo {
    width: 60px;
    margin-bottom: 1rem;
    border-radius: 50%;
  }
  
  .footer-links {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 3rem;
    flex-wrap: wrap;

      @media (min-width: 768px) {
        gap: 2rem; 
      }
  }
  
  .footer-links li {
    margin-bottom: 0.5rem;
  }
  
  .footer-links li a {
    color: #FFC857;
    text-decoration: none;
  }
  
  .footer-links li a:hover {
    text-decoration: underline;
  }
  
  .social-icons {
    display: flex;
    gap: 1rem; 
  }
  .footer-column_second{
    justify-content: flex-start;
    align-items: flex-start; 
  }
  
  .footer-bottom {
    text-align: start;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    padding-top: 1rem;
  }
  
  .footer-bottom p {
    margin: 0;
    font-size: 0.875rem;
  }
  
  /* For large screens */
  @media (min-width: 1024px) {
    .footer-content {
      flex-direction: row;
    }
  }
  
  
  //Navabar

  .container {
    max-width: 1200px;
    padding: 0 20px;
  }
  .banner{
    width: 100%;
    max-width: 1200px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .contacts{
        flex: 1;
        display: flex;
        justify-content: start;
        flex-direction: row;
        flex-wrap: nowrap;
        gap: 8px;
    }

  }
  
  a {
    transition: color 0.3s ease;
  }
  
  a:hover {
    color: $yellowAccent;
  }
  
  /* Contact Button */
  .bg-yellowAccent {
    background-color: $primary;
  }
  
  button:hover {
    border-radius: 5px;
  }
  
  /* Fullscreen Drawer Menu */
  .fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  nav a {
    transition: color 0.3s ease;
  }
  
  nav a:hover {
    color: $paleGreen;
  }



  //contact us
  .contact-us-section {
    display: flex;
    padding: 10vw;
    background-color: wheat;
    border-radius: 8px;
    width: 100vw;
  
    .contact-prompt {
      flex: 1;
      margin-right: 4rem;
      text-align: start;
      flex: 1;
  
      h2 {
        font-size: 2.5rem;
        margin-bottom: 1rem;
        color: $black;
      }
  
      p {
        font-size: 1.2rem;
        color: $secondary-color;
        max-width: 40ch;
      }
    }
  
    .contact-form {
        width: 50%;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end; 
        @media (max-width: 768px){
            width: 100%;
            justify-content: center;
            align-items: center;
        }
      form {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        max-width: 70%;
        width: 100%;
  
        .form-group {
          margin-bottom: 1.5rem;
  
          label {
            font-size: 1rem;
            margin-bottom: 0.5rem;
            display: block;
            color: $black;
            text-align: start;
          }
  
          input, textarea {
            width: 100%;
            padding: 1rem;
            border: .5px solid $secondary-color;
            border-radius: 8px;
            font-size: 1rem;
            outline: none;
            color: $black;
            background-color: rgba(245, 222, 179, 0);
  
            &:focus {
              border-color: $primary;
            }
          }
  
          textarea {
            resize: none;
          }
        }
  
        .primary-btn {
          padding: 1rem 2rem;
          background-color: $primary;
          color: $white;
          border: none;
          border-radius: 8px;
          cursor: pointer;
          transition: background-color 0.3s ease;
  
          &:hover {
            background-color: darken($primary, 10%);
          }
        }
      }
    }
  
    // Mobile responsiveness
    @media (max-width: 768px) {
      flex-direction: column;
  
      .contact-prompt {
        padding-right: 0;
        margin-bottom: 2rem;
        text-align: center; 
        width: 100%;
  
        h2 {
          font-size: 2rem;
        }
  
        p {
          font-size: 1rem;
          max-width: 100%;
          color: black;
        }
      }
  
      .contact-form {
        form {
          .form-group {
            margin-bottom: 1rem;
          }
  
          input, textarea {
            padding: 0.8rem;
          }
  
          .primary-btn {
            width: 100%;
          }
        }
      }
    }
  }
  


  // our team
$primary-bg-color: rgba(134, 16, 74, 0.9);
$overlay-color: rgba(0, 0, 0, 0.5);
$text-color: #fff;
$font-family: 'Helvetica, Arial, sans-serif';
$h3-font-size: 22px;
$h3-font-weight: 800;
$post-font-size: 17px;

.team-grid{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  justify-content: center;
  align-items: center;

  .team-header{
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 2rem;

    h2{
      font-size: 2rem;
    }

    p{max-width: 80ch;
    text-align: center;}
  }
}

.team-member {
  position: relative;
  text-align: center;
  color: #fff;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 100%; /* Ensures the component fills the container width */
  max-width: 300px; /* Control max width for grid responsiveness */
  margin: auto; /* Center alignment in grid */
}

.team-member img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ensures the image covers the area while maintaining aspect ratio */
  display: block;
  transition: transform 0.3s ease-in-out;
  max-height: 400px; /* Set max height to ensure uniformity */
}

.team-member:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease;
  opacity: 0;
}

.team-member:hover:before {
  opacity: 1;
}

.team-member:hover img {
  transform: scale(1.1); /* Zoom effect on hover */
}

.team-member .team-content {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 15px 0;
  background-color: rgba(134, 16, 74, 0.9);
  transition: all 0.3s ease;
  opacity: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-member:hover .team-content {
  opacity: 1;
  bottom: 20%; 
}

.team-member .name {
  font-size: 1.6rem;
  font-weight: 800;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.team-member .post {
  font-size: 1.2rem;
  text-transform: capitalize;
  letter-spacing: 0.5px;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .team-member {
    margin-bottom: 30px;
    max-width: 100%; /* Ensures component remains full-width on smaller screens */
  }

  .team-member img {
    max-height: 300px; /* Reduce height for smaller screens */
  }

  .team-content .name {
    font-size: 1.4rem;
  }

  .team-content .post {
    font-size: 1rem;
  }
}

